@import "variables";

body,
#iawp-parent {
    background-color: $color-gray-95;
}

#wpbody-content {
    padding-bottom: 0;
}

#wpbody-content > .error,
#wpbody-content > .notice {
    display: none;
}

#wpfooter {
    display: none;
}

.loading-icon {
    position: fixed;
    display: flex;
    z-index: $z99;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    justify-content: center;
    align-items: center;
    background: rgba(24, 20, 31, 0.65);
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s, visibility 0.3s;

    img {
        opacity: 0;
        transform: scale(0.9);
        transition: opacity 0.5s, transform 0.5s;
    }
}

#iawp-parent {
    // min-height: 120vh;
    margin-left: -10px;
    // padding-bottom: 60px;

    * {
        box-sizing: border-box;
    }

    &.loading {
        .loading-icon {
            opacity: 1;
            visibility: visible;

            img {
                opacity: 1;
                transform: scale(1);
            }
        }
    }

    @import "components/chart";
    @import "components/data_table";
    @import "components/header";
    @import "components/relative_dates";
    @import "components/toolbar";
    @import "components/real_time";
    @import "components/report_title_bar";

    @import "containers/campaigns";
    @import "containers/customize_columns";
    @import "containers/filters";
    @import "containers/input_group";
    @import "containers/interrupt";
    @import "containers/layout";
    @import "containers/micro_modal";
    @import "containers/modals";
    @import "containers/pagination";
    @import "containers/settings";
    @import "containers/stats";


    @import "sidebar/collapsed";
    @import "sidebar/logo";
    @import "sidebar/menu_section";
    @import "sidebar/pro_ad";
    @import "sidebar/sidebar";

    @import "elements/buttons";
    @import "elements/icons";
    @import "elements/images";
    @import "elements/inputs";
    @import "elements/labels";
    @import "elements/links";
    @import "elements/row_number";
    @import "elements/scroll_to_top";
    @import "elements/table_cells";
    @import "elements/text";
    @import "elements/tooltip";

    .geo-message {
        margin-left: 26px;
    }

    .iawp-sortable-ghost {
        opacity: .5;
    }
}

@import "dark_mode";
@import "html2pdf";
@import "support";
@import "updates";

@media all and (min-width: 783px) {

    #iawp-parent {
        margin-left: -20px;
    }
}

.toplevel_page_independent-analytics #fs_connect {
    @import "elements/buttons";
}

